<template>
  <div>
    <p class="editable--output" v-if="!editing" @click="startEditing" v-bind:class="[ !content ? 's-empty':'']">
      {{ content || placeholder }}
    </p>
    <div v-if="editing" class="editable--input" ref='textinput' contenteditable="true" @input="update" @keydown.enter="enterTiggered" @blur="doneEditing" :data-placeholder="placeholder"></div>
  </div>
</template>

<script>
export default {
  name: "Editable",
  props: ["content", "placeholder", "singleline"],
  data() {
    return {
      editing: false
    };
  },
  methods: {
    startEditing() {
      this.editing = true;
      if (typeof this.content != "undefined")
        this.$refs.textinput.innerText = this.content;
      this.$nextTick(() => {
        this.$refs.textinput.focus();
      });
    },
    update: function(event) {
      this.$emit("update", event.target.innerText);
    },
    enterTiggered(e) {
      if (this.singleline) {
        this.doneEditing();
        e.preventDefault();
      }
    },
    doneEditing() {
      this.$emit("doneEditing");
      this.editing = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../../scss/vars";
.editable--output {
  &.s-empty {
    color: palette(grey, 3);
    font-style: italic;
  }
}

.editable--input {
  margin-bottom: spacing(1);

  &:empty:before {
    content: attr(data-placeholder);
    font-style: italic;
    opacity: 0.6;
  }
}
</style>


