<template>
    <div>
        <div class="activityContent--section" v-if="comments.length > 0">
            <h5>Comments</h5>
            <Comment 
            v-for="comment in comments" 
            v-bind:key="comment.id"
            v-bind:comment="comment"/>
        </div>
        <div v-if="creating" class="">
            <input type="text" v-model="newCommentContent"/>
            <button @click="createComment">Send</button>
            <button @click="cancelCreateComment">Cancel</button>
        </div>
    </div>
</template>

<script>
import api from "./../api.js";

import Comment from "./Comment.vue";

export default {
  name: "Comments",
  props: ["comments", "apiURL"],
  components: {
    Comment
  },
  data: function() {
    return {
      creating: false,
      newCommentContent: ""
    };
  },
  mounted() {
    this.$root.$on("startCreateComment", this.startCreateComment);
  },
  beforeDestroy() {
    this.$root.$off("startCreateComment", this.startCreateComment);
  },
  methods: {
    createComment() {
      const comment = {
        content: this.newCommentContent
      };

      api
        .postOne(this.apiURL, comment)
        .then(result => {
          console.log(result);
          this.$emit("commentsUpdated");
        })
        .catch(err => console.log(err));
    },
    startCreateComment() {
      this.creating = true;
    },
    cancelCreateComment() {
      this.creating = false;
    }
  }
};
</script>

<style lang="scss" scoped>

</style>




