import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    user: null,
    activeWorkflow: null
  },
  mutations: {
    setUser(state, user) {
      state.user = user;
    },
    setActiveWorkflow(state, workflow) {
      state.activeWorkflow = workflow;
    }
  },
  actions: {
    // async addTodo({ commit }, todo) {
    //   console.log("Action: Trying to add todo");
    //   if (!todo || !todo.title) {
    //     console.error("Invalid todo item");
    //     return;
    //   }
    //   const result = await api.addTodo(todo);
    //   if (result === true) commit("addTodo", todo);
    //   else console.error("Could not add todo");
    // }
  },
  getters: {
    getUser: state => state.user
  }
});

export default store;



