<template>
  <div>
    <div class="workflow">
      <div class="workflow-details" v-if="activeWorkflow">        
        <WorkflowDetails
          v-bind:activeWorkflow="activeWorkflow"/>
      </div>
      
      <div v-if="activeWorkflow && !workflowActivities">Loading Tasks...</div>
      
      <div class="activities">
        <div v-if="workflowActivities && workflowActivities.length > 0">
          <draggable v-model="workflowActivities" @end="dragEnd" :options="{delay: 100, ghostClass: 'sortable-ghost', chosenClass: 'sortable-chosen', dragClass: 'sortable-drag', forceFallback: true}">
          <ActivityTile 
              v-for="activity in workflowActivities" 
              v-bind:key="activity.id"
              v-bind:apiURL="`/workflows/${activeWorkflow.id}/activities/${activity.id}`"
              v-bind:activity="activity"
              v-on:selectedActivityUpdated="loadActivity"
              v-on:activityDeleted="removeActivity"/>
          </draggable>
        </div>

        <ExpandingCreateControl
         v-bind:toggleButtonText="'Create Task'"
          v-bind:inputPlaceholder="'Task Title'"
          v-bind:createButtonText="'Add Task'"
          v-on:createTriggered="createActivity"
          />
        
      </div>
    </div>

    <div class="activity-overlay" v-if="activityid">
      <ActivityDetails 
        v-bind:activityid="activityid" 
        v-bind:workflowid="activeWorkflow.id"
        v-bind:nextActivityId="nextActivity"
        v-bind:prevActivityId="prevActivity"
        v-on:selectedActivityUpdated="loadActivity"/>
    </div>
</div>
</template>

<script>
import api from "./../api.js";
import store from "./../store.js";
import _ from "lodash";
import draggable from "vuedraggable";

import ActivityTile from "./ActivityTile.vue";
import ActivityDetails from "./ActivityDetails.vue";
import WorkflowDetails from "./WorkflowDetails.vue";

import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";

export default {
  name: "Workflow",
  props: ["apiURL"],
  components: {
    ActivityTile,
    ActivityDetails,
    WorkflowDetails,
    ExpandingCreateControl,
    draggable
  },
  data: function() {
    return {
      workflowActivities: null,

      activityid: null,
      nextActivity: null,
      prevActivity: null
    };
  },
  computed: {
    activeWorkflow() {
      return store.state.activeWorkflow;
    }
  },
  watch: {
    activeWorkflow() {
      this.loadWorkflowActivities();
    }
  },
  methods: {
    loadWorkflowActivities() {
      if (!this.activeWorkflow.id) return;

      this.workflowActivities = null; //Clear out the curent workflow so that we can load new ones (and show the user that we are)
      this.workflowComments = null;

      api
        .getAll(`workflows/${this.activeWorkflow.id}/activities/diagram`)
        .then(result => {
          this.workflowActivities = _.sortBy(
            result,
            o => o.diagramNode.position.positionY
          );
        })
        .catch(err => console.log(err));
    },

    createActivity(newActivityName) {
      if (newActivityName.length === 0) return;

      const positionY = _.last(this.workflowActivities)
        ? _.last(this.workflowActivities).diagramNode.position.positionY + 100
        : 5000;

      const newActivity = {
        type: "USER_TASK",
        positionX: 0,
        positionY: positionY,
        name: newActivityName
      };

      api
        .postOne(`workflows/${this.activeWorkflow.id}/activities/`, newActivity)
        .then(result => {
          console.log("success", result);
          this.workflowActivities.push(result);
        })
        .catch(err => console.log(err));
    },

    dragEnd(e) {
      e.preventDefault();

      const dropIndex = e.newIndex;
      const prevActivityY =
        dropIndex > 0
          ? this.workflowActivities[dropIndex - 1].diagramNode.position
              .positionY
          : null;
      const nextActivityY =
        dropIndex < this.workflowActivities.length - 1
          ? this.workflowActivities[dropIndex + 1].diagramNode.position
              .positionY
          : null;
      let diff = 0;

      if (prevActivityY && nextActivityY)
        diff = prevActivityY + Math.floor((nextActivityY - prevActivityY) / 2);
      else if (prevActivityY) diff = prevActivityY + 100;
      else if (nextActivityY) diff = nextActivityY - 100;
      else return;

      const newPosition = {
        activityId: this.workflowActivities[dropIndex].id,
        positionX: 0,
        positionY: diff
      };

      api
        .postOne(
          `workflows/${this.activeWorkflow.id}/activities/${
            this.workflowActivities[dropIndex].id
          }/position`,
          newPosition
        )
        .then(result => console.log("success", result))
        .catch(err => console.log(err));
    },

    loadActivity(id) {
      this.activityid = id;
      let index = _.findIndex(this.workflowActivities, o => o.id === id);

      this.nextActivity = this.workflowActivities[index + 1]
        ? this.workflowActivities[index + 1].id
        : null;
      this.prevActivity = this.workflowActivities[index - 1]
        ? this.workflowActivities[index - 1].id
        : null;
    },

    removeActivity(id) {
      console.log("removed" + id);
      _.remove(this.workflowActivities, { id: id });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.workflow {
  grid-area: main;
  padding: spacing(1);
  overflow-y: auto;
  display: grid;
  grid-template-rows: min-content auto;
  grid-template-areas:
    "workflowheader"
    "workflow";
}

.workflow-details {
  grid-area: workflowheader;
  padding-bottom: spacing(1);
  border-bottom: $default-border;
  margin-bottom: spacing(1);
}

.activities {
  grid-area: workflow;
}

.activity-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: zindex(modal);
}
</style>




