<template>
  <div class="activityContent--comment">
    <div class="comment--body">{{ comment.content }}</div>
    <label class="comment--label">{{ comment.metadata.userMetadata.modifiedBy.fullName }} - {{ comment.metadata.dateMetadata.modifiedOn }}</label>
</div>
</template>

<script>
export default {
  name: "Comment",
  props: ["comment"],
  data: function() {
    return {};
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.comment--body {
  margin-bottom: 0;
  padding: spacing(1) spacing(2);
  background-color: palette(grey, 1);
  border-radius: spacing(0.5);
}

.comment--label {
  @include label-small;
}
</style>



