import Vue from "vue";
import Router from "vue-router";

import Main from "./../components/Main.vue";
import Organization from "./../components/organization/Organization.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Main",
      component: Main
    },
    {
      path: "/org",
      name: "Organization",
      component: Organization
    }
  ]
});



