<template>
  <div id="app">
    <div class="logo">Day one yo</div>
    <div class="login" v-if="!user">
      <Login/>
    </div>
    <div class="logged-in" v-else>
      Welcome back <router-link to="/org">{{ user.firstName }} {{ user.lastName }}!</router-link>
    </div>

    <div class="main">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import store from "./store.js";

import Login from "./components/Login.vue";
import Main from "./components/Main.vue";

export default {
  name: "app",
  components: {
    Main,
    Login
  },
  data() {
    return {};
  },
  computed: {
    user() {
      return store.state.user;
    }
  }
};
</script>

<style lang="scss">
@import "./scss/vars.scss";

body {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;

  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: spacing(6) 1fr;
  grid-template-areas:
    "header-left header-right"
    "main main";
  min-height: 0;
  min-width: 0;

  width: 100vw;
  height: 100vh;

  @include breakpoint(smallmax) {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: spacing(6) 1fr;
    grid-template-areas:
      "header-left header-right"
      "main main";
  }
}

.logo {
  grid-area: header-left;
  justify-self: start;
  padding: spacing(1);
}

.login {
  grid-column: 1 / span 2;
  grid-row: 1 / span 3;
  z-index: zindex(modal);
  background-color: white;
  display: grid;
}

.logged-in {
  grid-area: header-right;
  justify-self: end;
  padding: spacing(1);
}

.main {
  grid-area: main;
  overflow: hidden;
  min-width: 0;
}
</style>



