<template>
    <div>
        <button v-if="!showCreateControl" @click="startCreate">{{ toggleButtonText }}</button>        
        <span v-if="showCreateControl">
        <input type="text" v-bind:placeholder="inputPlaceholder" v-model="inputContent" @input="checkHideInput" ref="inputContent" @keyup.enter="triggerCreate"/>
        <button @click="triggerCreate">{{ createButtonText }}</button>
        </span>
    </div>
</template>

<script>
export default {
  name: "ExpandingCreateControl",
  props: ["toggleButtonText", "inputPlaceholder", "createButtonText"],
  data: function() {
    return {
      creatingTimeout: null,
      inputContent: "",
      showCreateControl: false
    };
  },
  methods: {
    checkHideInput(e) {
      if (e.target.value.length === 0) this.countdownToHideInput();
      else clearTimeout(this.creatingTimeout);
    },

    startCreate() {
      this.showCreateControl = true;
      this.$nextTick(() => {
        this.$refs.inputContent.focus();
      });
      this.countdownToHideInput();
    },

    countdownToHideInput() {
      this.creatingTimeout = window.setTimeout(
        () => (this.showCreateControl = false),
        10000
      );
    },

    resetTaskCreate() {
      clearTimeout(this.creatingTimeout);
      this.inputContent = "";
      this.countdownToHideInput();
      this.$refs.inputContent.focus();
    },

    triggerCreate() {
      this.$emit("createTriggered", this.inputContent);
      this.resetTaskCreate();
    }
  }
};
</script>

<style lang="scss" scoped>

</style>




