<template>
  <div class="workflow-tile" v-bind:class="[activeWorkflowId === workflow.id ? 's-active' : '']" @click="setActiveWorkflowId">
      <h3>{{ workflow.process.name }}</h3>
      <div>State: {{ workflow.process.state }}</div>
      <div v-if="workflow.process.state === 'ACTIVE'">Progress: {{ workflow.stats.process.activities.states.COMPLETE || 0 }} / {{ workflow.stats.process.activities.count }} Tasks Complete</div>
  </div>
</template>

<script>
import store from "./../store.js";

export default {
  name: "WorkflowTile",
  props: ["workflow"],
  data: function() {
    return {};
  },
  computed: {
    activeWorkflowId() {
      return store.state.activeWorkflow ? store.state.activeWorkflow.id : null;
    }
  },
  methods: {
    setActiveWorkflowId() {
      store.commit("setActiveWorkflow", this.workflow);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.workflow-tile {
  @include containerBox;
  text-align: left;

  &:hover {
    background-color: palette(blue, 1);
  }

  &.s-active {
    background-color: palette(blue);
    color: white;
  }
}
</style>




