<template>
  <div class="container">
    
    <Workflows />
    
    <Workflow />

  </div>
</template>

<script>
import Workflow from "./Workflow.vue";
import Workflows from "./Workflows.vue";

export default {
  name: "Main",
  components: {
    Workflow,
    Workflows
  },
  data: function() {
    return {
      workflows: null,
      workflowid: null
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "./../scss/vars";

.container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "sidebar main";

  width: 100%;
  height: 100%;

  @include breakpoint(smallmax) {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "main main";
  }
}

.workflow-list {
  list-style-type: none;
  margin: 0;
  padding: spacing(1);
}
</style>



