<template>
  <div>
    <div class="activityContent--section users" v-if="users.length > 0">
      <div class="users--assigned">
        <h5>Assigned User</h5>
        <div class="users--user" v-for="user in assignedUser" v-bind:key="user.id">
          <User :user="user" :view-mode="'DETAILED'"/>
        </div>
      </div>
      <div class="users--subscribed">
        <h5>Subscribed Users</h5>
        <div class="users--user" v-for="user in subscribedUsers" v-bind:key="user.id">
          <User :user="user"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//import api from "./../api.js";
import _ from "lodash";

import User from "./User.vue";

export default {
  name: "Users",
  props: ["users"],
  components: { User },
  data: function() {
    return {
      assignedUser: _.filter(
        this.users,
        o => o.assignedAction === "EXECUTE"
      ).map(x => x.assignedTo),
      subscribedUsers: _.filter(
        this.users,
        o => o.assignedAction === "SUBSCRIBE"
      ).map(x => x.assignedTo)
    };
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.users {
  display: grid;
  grid-template-columns: min-content auto;
  grid-gap: spacing(2);
}

.users--assigned {
  white-space: nowrap;
}

.users--user {
  display: inline-block;
}
</style>



