<template>
    <div>
        <h1>Organization</h1>
        {{ user }}
    </div>
</template>

<script>
//import api from "./../../api.js";
import store from "./../../store.js";

export default {
  name: "Organization",
  data() {
    return {
      organization: null,
      contacts: null
    };
  },
  computed: {
    user() {
      return store.state.user;
    }
  },
  created() {}
};
</script>

<style lang="scss" scoped>
@import "./../../scss/vars.scss";
</style>

 


