<template>
    <div class="sidebar">
      <div v-if="workflows && workflows.length > 0">
        <h5>Workflows</h5>
        <hr/>
        <WorkflowTile 
            v-for="workflow in workflows" 
            v-bind:key="workflow.id" 
            v-bind:workflow="workflow"/>
      </div>

      <ExpandingCreateControl
         v-bind:toggleButtonText="'Create Workflow'"
          v-bind:inputPlaceholder="'Workflow Name'"
          v-bind:createButtonText="'Add Workflow'"
          v-on:createTriggered="createWorkflow"
          />

    </div>
  
</template>

<script>
import api from "./../api.js";
import store from "./../store.js";

import WorkflowTile from "./WorkflowTile.vue";

import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";

export default {
  name: "Workflows",
  props: ["apiURL"],
  components: {
    WorkflowTile,
    ExpandingCreateControl
  },
  data: function() {
    return {
      workflows: null
    };
  },
  computed: {
    user() {
      return store.state.user;
    }
  },
  watch: {
    user() {
      this.getFlows();
    }
  },
  methods: {
    getFlows() {
      api
        .getAll("workflows")
        .then(result => {
          this.workflows = result;
        })
        .catch(err => console.log(err));
    },
    createWorkflow(newWorkflowName) {
      const workflow = {
        diagramType: "LISTCHART",
        executionOrder: "POSITION",
        name: newWorkflowName,
        state: "DESIGNING"
      };

      api
        .postOne("workflows", workflow)
        .then(result => {
          console.log(result);
          this.workflows.push(result);
          this.$emit("workflowsUpdated", result);
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.sidebar {
  grid-area: sidebar;
  padding: spacing(1);
  overflow-y: auto;

  @include breakpoint(smallmax) {
    grid-area: main;
  }
}
</style>




