<template>
  <div class="activityContent--checklist">
    <h5 class="checklist--name">{{ checklist.name }}</h5>
    <div class="checklist--list">
      <draggable v-model="checkListItems" @end="dragEnd" :options="{delay:100, handle: '.drag-handle', ghostClass: 'sortable-ghost', chosenClass: 'sortable-chosen', dragClass: 'sortable-drag', forceFallback: true }">
        <ChecklistItem 
          v-for="todo in checkListItems"
          v-bind:key="todo.id"
          v-bind:todo="todo"
          v-bind:url="todoURL"/>
      </draggable>
      <div v-if="creatingNewTodo">
        <input type="text" v-model="newTodoText">
        <button @click="createTodo">Create</button>
        <button @click="cancelCreateTodo">Cancel</button>
      </div>
      <button class="add" v-else @click="()=>creatingNewTodo = true">+</button>
    </div>
    <label class="checklist--label">Created by: {{ checklist.metadata.userMetadata.modifiedBy.fullName }} - {{ checklist.metadata.dateMetadata.modifiedOn }}</label>
</div>
</template>

<script>
import api from "./../api.js";
import draggable from "vuedraggable";
import _ from "lodash";

import ChecklistItem from "./ChecklistItem.vue";

export default {
  name: "Checklist",
  props: ["checklist", "apiURL", "creating"],
  components: { draggable, ChecklistItem },
  data: function() {
    return {
      todoURL: `${this.apiURL}/${this.checklist.id}/items/`,
      checkListItems: null,
      creatingNewTodo: false,
      newTodoText: ""
    };
  },
  created() {
    this.checkListItems = _.sortBy(this.checklist.items, o => o.position);
    this.creatingNewTodo = this.checkListItems.length === 0;
    // if(this.creating) this.creatingNewTodo = true;
  },
  methods: {
    cancelCreateTodo() {
      this.creatingNewTodo = false;
      this.newTodoText = "";
    },
    createTodo() {
      const position =
        this.checkListItems.length > 0
          ? _.last(this.checkListItems).position + 1000
          : 1000;

      const newTodo = {
        name: this.newTodoText,
        state: "INCOMPLETE",
        position: position
      };

      api
        .postOne(this.todoURL, newTodo)
        .then(result => {
          this.cancelCreateTodo();
          this.checkListItems.push(result);
          console.log("success", result);
        })
        .catch(err => console.log(err));
    },
    dragEnd(e) {
      const dropIndex = e.newIndex;
      const prevItemY =
        dropIndex > 0 ? this.checkListItems[dropIndex - 1].position : null;
      const nextItemY =
        dropIndex < this.checkListItems.length - 1
          ? this.checkListItems[dropIndex + 1].position
          : null;
      let diff = 0;

      if (prevItemY && nextItemY)
        diff = prevItemY + Math.floor((nextItemY - prevItemY) / 2);
      else if (prevItemY) diff = prevItemY + 100;
      else if (nextItemY) diff = nextItemY - 100;
      else return;

      let update = {
        position: diff
      };

      api
        .updateOne(
          `${this.todoURL}/${this.checkListItems[dropIndex].id}`,
          update
        )
        .then(result => console.log("success", result))
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.checklist--list {
  padding-left: spacing(2);
}

.checklist--label {
  @include label-small;
}
</style>



