<template>
  <div>
    <div class="activityContent--section" v-if="attachments.length > 0">
      <h5>Attachments</h5>
      <div class="attachment" v-for="attachment in attachments" v-bind:key="attachment.id">
        <div v-if="getMimeType(attachment.mimeType) === 'IMAGE'">
           <img v-bind:src="`http://qa.feralcatlabs.com/download/attachment/${attachment.location}`"/>
           <p class="attachment--label">{{ attachment.fileName }}</p>
        </div>
        <div v-else>
          <a v-bind:href="`http://qa.feralcatlabs.com/download/attachment/${attachment.location}`">{{ attachment.fileName }}</a>
        </div>
        <button class="attachment--delete" @click="deleteAttachment(attachment.id)">Delete</button>
        <p class="attachment--label">{{ attachment.metadata.userMetadata.modifiedBy.fullName }} - {{ attachment.metadata.dateMetadata.modifiedOn }}</p>
      </div>
    </div>
    <div v-if="creating">  
      <input type="text" v-model="newHyperlinkLink" placeholder="URL"/>
      <input type="text" v-model="newHyperlinkText" placeholder="Description"/>
      <button @click="createAttachment">Save</button>
      <button @click="cancelCreateAttachment">Cancel</button>
    </div>
  </div>
</template>

<script>
import api from "./../api.js";

export default {
  name: "Attachments",
  props: ["attachments", "apiURL"],
  data: function() {
    return {
      creating: false,
      newHyperlinkLink: "",
      newHyperlinkText: ""
    };
  },
  mounted() {
    this.$root.$on("startCreateAttachment", this.startCreateAttachment);
  },
  beforeDestroy() {
    this.$root.$off("startCreateAttachment", this.startCreateAttachment);
  },
  methods: {
    deleteAttachment(id) {
      api
        .deleteOne(`${this.apiURL}/${id}`)
        .then(result => {
          this.$emit("attachmentsUpdated");
          console.log(result);
        })
        .catch(err => console.log(err));
    },
    createAttachment() {
      console.log("TODO: CREATE ATTACHMENT");
      // const hyperlink = {
      //   title: this.newHyperlinkText,
      //   address: this.newHyperlinkLink
      // };

      // api
      //   .postOne(this.apiURL, hyperlink)
      //   .then(result => {
      //     console.log(result);
      //     this.$emit("hyperlinksUpdated");
      //     this.cancelCreateHyperlink();
      //   })
      //   .catch(err => console.log(err));
    },
    startCreateAttachment() {
      this.creating = true;
    },
    cancelCreateAttachment() {
      this.creating = false;
    },
    getMimeType(mimeType) {
      switch (mimeType) {
        case "image/gif":
        case "image/jpgeg":
        case "image/png":
        case "image/svg+xml":
          return "IMAGE";

        case "application/vnd.ms-excel":
        case "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet":
        case "text/csv":
          return "SPREADSHEET";

        case "application/vnd.openxmlformats-officedocument.presentationml.presentation":
        case "application/vnd.ms-powerpoint":
          return "PRESENTATION";

        case "application/msword":
        case "application/vnd.openxmlformats-officedocument.wordprocessingml.document":
          return "DOCUMENT";

        case "application/pdf":
          return "PDF";

        case "application/zip":
        case "application/x-rar-compressed":
          return "ARCHIVE";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";
.attachment {
  &:hover {
    .attachment--delete {
      visibility: visible;
    }
  }
}

.attachment--delete {
  visibility: hidden;
}

.attachment--label {
  @include label-small;
}
</style>



