<template>
    <div class="activityContent--section" v-if="checklists.length > 0">
        <Checklist 
            v-for="checklist in checklists" 
            v-bind:key="checklist.id"
            v-bind:checklist="checklist"
            v-bind:apiURL="apiURL"/>
    </div>
</template>

<script>
import api from "./../api.js";
import _ from "lodash";

import Checklist from "./Checklist.vue";

export default {
  name: "Checklists",
  props: ["checklists", "apiURL"],
  components: {
    Checklist
  },
  mounted() {
    this.$root.$on("createChecklist", this.createChecklist);
  },
  beforeDestroy() {
    this.$root.$off("createChecklist", this.createChecklist);
  },
  methods: {
    createChecklist() {
      const position =
        this.checklists.length > 0
          ? _.last(this.checklists).position + 1000
          : 1000;

      const checklist = {
        name: "Checklist",
        position: position
      };

      api
        .postOne(this.apiURL, checklist)
        .then(result => {
          console.log(result);
          this.checklists.push(result);
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>

</style>




