<template>
    <div class="activityDetails" @click="changeActivity(null)">
        <button class="prev-button" v-if="prevActivityId" @click.stop="changeActivity(prevActivityId)">Previous Activity</button>
        <div class="activityModal" @click.stop="">
          <div class="activityContent" v-if="activityDetails">
            <h3>{{ activityDetails.name }}</h3>
            
            <Editable
              v-bind:content="activityDetails.description"
              v-bind:placeholder="'Description'"
              v-bind:singleline="true"
              v-on:update="activityDetails.description = $event"
              v-on:doneEditing="updateDescription"/>

            <h4>Status: {{ activityDetails.state }}</h4>
            <p>Created By: {{ activityDetails.metadata.userMetadata.modifiedBy.fullName }}</p>
            
            <ActivityUsers
              v-bind:users="activityDetails.assignments"/>

            <Checklists
              v-bind:checklists="activityDetails.checklists"
              v-bind:apiURL="`workflows/${workflowid}/activities/${activityid}/checklists`"/>
            
            <Hyperlinks 
              v-bind:hyperlinks="activityDetails.hyperlinks" 
              v-bind:apiURL="`workflows/${workflowid}/activities/${activityid}/hyperlinks`"
              v-on:hyperlinksUpdated="hyperlinksUpdated"/>

            <Attachments 
              v-bind:attachments="activityDetails.attachments" 
              v-bind:apiURL="`workflows/${workflowid}/activities/${activityid}/attachments`"
              v-on:hyperlinksUpdated="attachmentsUpdated"/>
            
            <Comments
              v-bind:comments="activityDetails.comments" 
              v-bind:apiURL="`workflows/${workflowid}/activities/${activityid}/comments`"
              v-on:commentsUpdated="commentsUpdated"/>
            
            <div>
              <button @click="() => this.$root.$emit('createChecklist')">Add a Checklist</button>
              <button @click="() => this.$root.$emit('startCreateComment')">Comment</button>
              <button @click="() => this.$root.$emit('startCreateHyperlink')">Add a Link</button>
            </div>
          </div>  
          <div v-else>
            <h3>Loading {{ activityid }}</h3>
          </div>
        </div>
        <button class="next-button" v-if="nextActivityId" @click.stop="changeActivity(nextActivityId)">Next Activity</button>
    </div>
</template>

<script>
import api from "./../api.js";

import Comments from "./Comments.vue";
import Hyperlinks from "./Hyperlinks.vue";
import Attachments from "./Attachments.vue";
import Checklists from "./Checklists.vue";
import ActivityUsers from "./ActivityUsers.vue";

import Editable from "./controls/Editable.vue";

export default {
  name: "ActivityDetails",
  props: ["workflowid", "activityid", "nextActivityId", "prevActivityId"],
  components: {
    Comments,
    Hyperlinks,
    Attachments,
    Checklists,
    ActivityUsers,
    Editable
  },
  created: function() {
    this.getActivityDetails(this.activityid);
  },
  watch: {
    activityid: function(newVal) {
      this.activityDetails = null;
      this.getActivityDetails(newVal);
    }
  },
  data: function() {
    return {
      activityDetails: null,
      creatingComment: false,
      creatingHyperlink: false
    };
  },
  methods: {
    changeActivity(id) {
      this.$emit("selectedActivityUpdated", id);
    },
    getActivityDetails(id) {
      api
        .getAll(`workflows/${this.workflowid}/activities/${id}/details`)
        .then(result => {
          this.activityDetails = result;
        })
        .catch(err => console.log(err));
    },
    updateDescription() {
      const update = {
        description: this.activityDetails.description,
        type: "USER_TASK"
      };

      api
        .updateOne(
          `workflows/${this.workflowid}/activities/${this.activityid}`,
          update
        )
        .then(result => {
          console.log(result);
        })
        .catch(err => console.log(err));
    },
    commentsUpdated() {
      console.log("comments Updated");
    },
    hyperlinksUpdated() {
      api
        .getAll(
          `workflows/${this.workflowid}/activities/${
            this.activityid
          }/hyperlinks`
        )
        .then(result => {
          this.activityDetails.hyperlinks = result;
        })
        .catch(err => console.log(err));
    },
    attachmentsUpdated() {
      api
        .getAll(
          `workflows/${this.workflowid}/activities/${
            this.activityid
          }/attachments`
        )
        .then(result => {
          this.activityDetails.attachments = result;
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss">
@import "./../scss/vars";

.activityDetails {
  @include modal-background;
}
.activityModal {
  @include modal-window;
}

.activityContent--section {
  margin-top: spacing(2);
  padding-top: spacing(2);
  border-top: $default-border;
}

.prev-button {
  justify-self: end;
  grid-area: left;
}

.next-button {
  justify-self: start;
  grid-area: right;
}
</style>



