<template>
  <div>
    <div class="activityContent--section users" v-if="users.length > 0">
      
      <div class="users--subscribed">
        <h5>Participating Users</h5>
        <div class="users--user" v-for="user in users" v-bind:key="user.id">
          <User :user="user"/>
        </div>
      </div>

      <div class="users--subscribed" v-if="placeholders && placeholders.length > 0">
        <h5>Placeholders</h5>
        <div class="users--user" v-for="placeholder in placeholders" v-bind:key="placeholder.id">
          {{ placeholder.name }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//import api from "./../api.js";
//import _ from "lodash";

import User from "./User.vue";

export default {
  name: "Users",
  props: ["users", "placeholders"],
  components: { User },
  data: function() {
    return {
      //   assignedUser: _.filter(this.users, o => o.assignedAction === "EXECUTE"),
      //   subscribedUsers: _.filter(
      //     this.users,
      //     o => o.assignedAction === "SUBSCRIBE"
      //   )
    };
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";
</style>



