import axios from "axios";

const axinstance = axios.create({
  baseURL: "http://qa.feralcatlabs.com/api/"
});

export default {
  login: function(email, password) {
    return new Promise(function(resolve, reject) {
      const login = {
        clientId: "0",
        persistLogin: true,
        emailAddress: email,
        password: password
      };

      axinstance
        .post("login", login)
        .then(function(response) {
          localStorage.setItem(
            "authToken",
            response.data.response.authenticationToken
          );
          axinstance.defaults.headers.common["AuthenticationToken"] =
            response.data.response.authenticationToken;
          resolve(response.data.user);
        })
        .catch(err => reject(err));
    });
  },
  checkToken: function(path, token) {
    return new Promise(function(resolve, reject) {
      axinstance.defaults.headers.common["AuthenticationToken"] = token;

      axinstance
        .get(path)
        .then(function(response) {
          resolve(response.data);
        })
        .catch(err => reject(err));
    });
  },
  getAll: function(path) {
    return new Promise(function(resolve, reject) {
      axinstance
        .get(path)
        .then(function(response) {
          resolve(response.data);
        })
        .catch(err => reject(err));
    });
  },
  postOne: function(path, data) {
    return new Promise(function(resolve, reject) {
      axinstance
        .post(path, data)
        .then(function(response) {
          resolve(response.data);
        })
        .catch(err => reject(err));
    });
  },
  updateOne: function(path, data) {
    return new Promise(function(resolve, reject) {
      axinstance
        .put(path, data)
        .then(function(response) {
          resolve(response.data);
        })
        .catch(err => reject(err));
    });
  },
  deleteOne: function(path) {
    return new Promise(function(resolve, reject) {
      axinstance
        .delete(path)
        .then(function(response) {
          resolve(response.data);
        })
        .catch(err => reject(err));
    });
  }
};



