<template>
    <div class="activity-tile" @click="()=> this.$emit('selectedActivityUpdated',activity.id)">
        <h3 class="activity-tile--title">{{ activity.name }}</h3>
        <div class="activity-tile--action"><button @click.stop="deleteActivity">Delete</button></div>
        <div class="activity-tile--state">State: {{ activity.state }}</div>
        <div class="activity-tile--meta">Created By: {{ activity.metadata.userMetadata.modifiedBy.fullName }}</div>
    </div>
</template>

<script>
import api from "./../api.js";

export default {
  name: "ActivityTile",
  props: ["activity", "apiURL", "loadActivity"],
  data: function() {
    return {};
  },
  methods: {
    deleteActivity() {
      api
        .deleteOne(this.apiURL)
        .then(result => {
          this.$emit("activityDeleted", this.activity.id);
          console.log(result);
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss">
@import "./../scss/vars";

.activity-tile {
  @include containerBox;
  padding: spacing(2) spacing(3);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "header header header header-right"
    "state meta meta meta";
  text-align: left;

  &:hover {
    .activity-tile--action {
      visibility: visible;
    }
  }
}

.activity-tile--title {
  grid-area: header;
  padding: 0;
  margin: 0 0 spacing(2);
}

.activity-tile--state {
  grid-area: state;
  font-weight: 700;
}

.activity-tile--meta {
  grid-area: meta;
}

.activity-tile--action {
  grid-area: header-right;
  visibility: hidden;
}
</style>



