<template>
    <div>
        <h4>{{ user.user.fullName }}</h4>
        <div class="user--avatar__photo" v-if="user.user.imageUrl"><img v-bind:src="user.user.imageUrl"/></div>
        <div class="user--avatar__initials" v-else>{{ userNameInitials(user.user.fullName) }}</div>
        <div class="user--label" v-if="viewMode === 'DETAILED'">{{ user.userAccess }}</div>
    </div>
</template>

<script>
export default {
  name: "User",
  props: ["view-mode", "user"],
  methods: {
    userNameInitials(fullName) {
      const names = fullName.split(" ");
      return names[0].charAt(0) + names[1].charAt(0);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.user--label {
  @include label-small;
}
</style>



