<template>
    <div class="workflow--details">
        <h5 class="workflow--title" @click="toggleWorkflowDetails">
            {{ activeWorkflow.process.name }} - {{ activeWorkflow.process.state }} - {{ activeWorkflow.stats.process.activities.count }} Tasks 
        </h5>
        
        <div class="workflow--actions">
            <button v-if="this.workflowPlaceholders || this.workflowUsers" @click="toggleWorkflowUsers">{{ allUsersCount }} Users</button>
            <button v-if="activeWorkflow.stats.comments.count > 0" @click="toggleWorkflowComments">- {{ activeWorkflow.stats.comments.count }} Comments</button>
            <button v-if="activeWorkflow.stats.process.hyperlinks.count > 0" @click="toggleWorkflowHyperlinks">- {{ activeWorkflow.stats.process.hyperlinks.count }} Links</button>
            <button v-if="activeWorkflow.stats.process.attachments.count > 0" @click="toggleWorkflowAttachments">- {{ activeWorkflow.stats.process.attachments.count }} Attachments</button>
        </div>
        
        <div class="workflow--details-contents" v-if="activeTab === 'DETAILS'">
        <div>
            Created: {{ activeWorkflow.metadata.dateMetadata.createdOn }}
            <br/>Last Modified: {{ activeWorkflow.metadata.dateMetadata.modifiedOn }} by {{ activeWorkflow.metadata.userMetadata.modifiedBy.fullName }}
        </div>
        <div v-if="activeWorkflow.process.description">
            Description: {{ activeWorkflow.process.description }}
        </div>
        <div>
            {{ activeWorkflow.stats.process.activities.count }} Tasks
            <ul>
            <li v-for="(value, key) in activeWorkflow.stats.process.activities.states" v-bind:key="key">{{ key }}:{{ value }}</li>
            </ul>
        </div>
        </div>

         <div class="workflow--details-contents" v-if="activeTab === 'USERS'">
          <div v-if="!workflowUsers">Loading Users...</div>
          <WorkflowUsers
            v-else
            v-bind:placeholders="workflowPlaceholders"
            v-bind:users="workflowUsers" 
            v-bind:apiURL="`workflows/${activeWorkflow.id}/comments`"
            v-on:commentsUpdated="loadWorkflowComments"/>

         </div>

        <div class="workflow--details-contents" v-if="activeTab === 'COMMENTS'">
          <div v-if="!workflowComments">Loading Comments...</div>
          <Comments
              v-else
              v-bind:comments="workflowComments" 
              v-bind:apiURL="`workflows/${activeWorkflow.id}/comments`"
              v-on:commentsUpdated="loadWorkflowComments"/>
        </div>

        <div class="workflow--details-contents" v-if="activeTab === 'HYPERLINKS'">
          <div v-if="!workflowHyperlinks">Loading Links...</div>
          <Hyperlinks 
              v-else
              v-bind:hyperlinks="workflowHyperlinks" 
              v-bind:apiURL="`workflows/${activeWorkflow.id}/hyperlinks`"
              v-on:hyperlinksUpdated="loadWorkflowHyperlinks"/>
        </div>

        <div class="workflow--details-contents" v-if="activeTab === 'ATTACHMENTS'">
          <div v-if="!workflowAttachments">Loading Attachments...</div>
          <Attachments 
              v-else
              v-bind:attachments="workflowAttachments" 
              v-bind:apiURL="`workflows/${activeWorkflow.id}/attachments`"
              v-on:attachmentsUpdated="loadWorkflowAttachments"/>
        </div>
    </div>
</template>

<script>
import api from "./../api.js";

import Comments from "./Comments.vue";
import Hyperlinks from "./Hyperlinks.vue";
import Attachments from "./Attachments.vue";
import WorkflowUsers from "./WorkflowUsers.vue";

export default {
  name: "WorkflowDetails",
  props: ["activeWorkflow"],
  components: {
    Comments,
    Hyperlinks,
    Attachments,
    WorkflowUsers
  },
  data: function() {
    return {
      activeTab: null,
      workflowComments: null,
      workflowHyperlinks: null,
      workflowAttachments: null,
      workflowUsers: null,
      workflowPlaceholders: null
    };
  },
  created() {
    this.loadWorkflowUsers();
  },
  computed: {
    allUsersCount() {
      if (this.workflowPlaceholders && this.workflowUsers)
        return this.workflowPlaceholders.length + this.workflowUsers.length;
      else if (this.workflowPlaceholders)
        return this.workflowPlaceholders.length;
      else if (this.workflowUsers) return this.workflowUsers.length;
      else return 0;
    }
  },
  methods: {
    toggleWorkflowDetails() {
      this.activeTab = this.activeTab === "DETAILS" ? null : "DETAILS";
    },

    toggleWorkflowUsers() {
      this.activeTab = this.activeTab === "USERS" ? null : "USERS";
      if (this.activeTab === "USERS") this.loadWorkflowUsers();
    },
    loadWorkflowUsers() {
      const users = api.getAll(`workflows/${this.activeWorkflow.id}/users`);

      const placeholders = api.getAll(
        `workflows/${this.activeWorkflow.id}/placeholders`
      );

      Promise.all([users, placeholders])
        .then(results => {
          this.workflowUsers = results[0];
          this.workflowPlaceholders = results[1];
        })
        .catch(err => console.log(err));
    },

    toggleWorkflowComments() {
      this.activeTab = this.activeTab === "COMMENTS" ? null : "COMMENTS";
      if (this.activeTab === "COMMENTS") this.loadWorkflowComments();
    },
    loadWorkflowComments() {
      api
        .getAll(`workflows/${this.activeWorkflow.id}/comments`)
        .then(result => {
          this.workflowComments = result;
        })
        .catch(err => console.log(err));
    },

    toggleWorkflowHyperlinks() {
      this.activeTab = this.activeTab === "HYPERLINKS" ? null : "HYPERLINKS";
      if (this.activeTab === "HYPERLINKS") this.loadWorkflowHyperlinks();
    },
    loadWorkflowHyperlinks() {
      api
        .getAll(`workflows/${this.activeWorkflow.id}/hyperlinks`)
        .then(result => {
          this.workflowHyperlinks = result;
        })
        .catch(err => console.log(err));
    },

    toggleWorkflowAttachments() {
      this.activeTab = this.activeTab === "ATTACHMENTS" ? null : "ATTACHMENTS";
      if (this.activeTab === "ATTACHMENTS") this.loadWorkflowAttachments();
    },
    loadWorkflowAttachments() {
      api
        .getAll(`workflows/${this.activeWorkflow.id}/attachments`)
        .then(result => {
          this.workflowAttachments = result;
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
.workflow--details {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-areas:
    "details-title details-actions"
    "details-content details-content";
}

.workflow--details-contents {
  grid-area: details-content;
}

.workflow--actions {
  grid-area: details-actions;
}

.workflow--title {
  grid-area: details-title;
}
</style>




