<template>
  <div class="login">
    <div v-if="attemptLogin">
      Checking Credentials...
    </div>
    <div v-else>
      <input type="email" v-model="email" placeholder="email"/>
      <input type="password" v-model="password" placeholder="password"/>
      <button @click="tryLogin()" :disabled="loggingIn">Login Plz</button>

      <div class="login--error" v-if="loginError">
        {{ loginError }}
      </div>
    </div>
    
</div>
</template>

<script>
import api from "./../api.js";
import store from "./../store.js";

export default {
  name: "Login",
  data: function() {
    return {
      email: null,
      password: null,
      loggingIn: false,
      user: null,
      loginError: null,
      attemptLogin: false
    };
  },
  props: ["setLoggedIn"],
  created: function() {
    let token = localStorage.getItem("authToken") || null;
    if (token === null) return;

    this.attemptLogin = true;

    api
      .checkToken("login/user", token)
      .then(result => {
        this.user = result;
        this.attemptLogin = false;
        store.commit("setUser", result);
      })
      .catch(err => {
        console.error(err);
        this.attemptLogin = false;
        this.loginError = err;
      });
  },

  methods: {
    tryLogin() {
      this.loggingIn = true;
      this.loginError = null;

      api
        .login(this.email, this.password)
        .then(result => {
          this.user = result;
          this.loggingIn = false;
          store.commit("setUser", result);
        })
        .catch(err => {
          console.error(err);
          this.loggingIn = false;
          this.loginError = err;
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

input {
  display: block;
  height: spacing(5);
  line-height: spacing(5);
  margin-bottom: spacing(1);
  padding: 0 spacing(2);
  width: 200px;
  appearance: none;
  border: $default-border;
}

.login {
  justify-self: center;
  align-self: center;
  max-width: 600px;
}

.login--error {
  background-color: palette(red);
  color: white;
  padding: spacing(2);
}
</style>



