<template>
  <div>
    <div class="activityContent--section" v-if="hyperlinks.length > 0">
      <h5>Links</h5>
      <div class="hyperlink" v-for="link in hyperlinks" v-bind:key="link.id">
        <a v-bind:href="link.address">{{ link.title }}</a>
        <button class="hyperlink--delete" @click="deleteHyperlink(link.id)">Delete</button>
        <p class="hyperlink--label">{{ link.metadata.userMetadata.modifiedBy.fullName }} - {{ link.metadata.dateMetadata.modifiedOn }}</p>
      </div>
    </div>
    <div v-if="creating">  
      <input type="text" v-model="newHyperlinkLink" placeholder="URL"/>
      <input type="text" v-model="newHyperlinkText" placeholder="Description"/>
      <button @click="createHyperlink">Save</button>
      <button @click="cancelCreateHyperlink">Cancel</button>
    </div>
  </div>
</template>

<script>
import api from "./../api.js";

export default {
  name: "Hyperlinks",
  props: ["hyperlinks", "apiURL"],
  data: function() {
    return {
      creating: false,
      newHyperlinkLink: "",
      newHyperlinkText: ""
    };
  },
  mounted() {
    this.$root.$on("startCreateHyperlink", this.startCreateHyperlink);
  },
  beforeDestroy() {
    this.$root.$off("startCreateHyperlink", this.startCreateHyperlink);
  },
  methods: {
    deleteHyperlink(id) {
      api
        .deleteOne(`${this.apiURL}/${id}`)
        .then(result => {
          this.$emit("hyperlinksUpdated");
          console.log(result);
        })
        .catch(err => console.log(err));
    },
    createHyperlink() {
      const hyperlink = {
        title: this.newHyperlinkText,
        address: this.newHyperlinkLink
      };

      api
        .postOne(this.apiURL, hyperlink)
        .then(result => {
          console.log(result);
          this.$emit("hyperlinksUpdated");
          this.cancelCreateHyperlink();
        })
        .catch(err => console.log(err));
    },
    startCreateHyperlink() {
      this.creating = true;
    },
    cancelCreateHyperlink() {
      this.creating = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";
.hyperlink {
  &:hover {
    .hyperlink--delete {
      visibility: visible;
    }
  }
}

.hyperlink--delete {
  visibility: hidden;
}

.hyperlink--label {
  @include label-small;
}
</style>



